body {
  background-color: #ffffff;
  color: #333333;
}
a,
a:visited {
  color: #f48a22;
}
a:hover,
a:focus,
a:active {
  color: #f48a22;
}
body .grid-container {
  max-width: 1200px;
}
body,
button,
input,
select,
textarea {
  font-size: 16px;
}
.entry-content > [class*="wp-block-"]:not(:last-child) {
  margin-bottom: 1.5em;
}
.main-navigation .main-nav ul ul li a {
  font-size: 14px;
}
h1 {
  font-weight: normal;
  font-size: 50px;
}
h2 {
  font-weight: normal;
  font-size: 40px;
}
h3 {
  font-size: 30px;
}
@media (max-width: 768px) {
  .main-title {
    font-size: 30px;
  }
  h1 {
    font-size: 30px;
  }
  h2 {
    font-size: 25px;
  }
}
.top-bar {
  background-color: #636363;
  color: #ffffff;
}
.top-bar a,
.top-bar a:visited {
  color: #ffffff;
}
.top-bar a:hover {
  color: #303030;
}
.site-header {
  background-color: #ffffff;
  color: #3a3a3a;
}
.site-header a,
.site-header a:visited {
  color: #3a3a3a;
}
.main-title a,
.main-title a:hover,
.main-title a:visited {
  color: #222222;
}
.site-description {
  color: #757575;
}
.main-navigation,
.main-navigation ul ul {
  background-color: #222222;
}
.main-navigation .main-nav ul li a,
.menu-toggle {
  color: #ffffff;
}
.main-navigation .main-nav ul li:hover > a,
.main-navigation .main-nav ul li:focus > a,
.main-navigation .main-nav ul li.sfHover > a {
  color: #ffffff;
  background-color: #3f3f3f;
}
button.menu-toggle:hover,
button.menu-toggle:focus,
.main-navigation .mobile-bar-items a,
.main-navigation .mobile-bar-items a:hover,
.main-navigation .mobile-bar-items a:focus {
  color: #ffffff;
}
.main-navigation .main-nav ul li[class*="current-menu-"] > a {
  color: #ffffff;
  background-color: #3f3f3f;
}
.main-navigation .main-nav ul li[class*="current-menu-"] > a:hover,
.main-navigation .main-nav ul li[class*="current-menu-"].sfHover > a {
  color: #ffffff;
  background-color: #3f3f3f;
}
.navigation-search input[type="search"],
.navigation-search input[type="search"]:active {
  color: #3f3f3f;
  background-color: #3f3f3f;
}
.navigation-search input[type="search"]:focus {
  color: #ffffff;
  background-color: #3f3f3f;
}
.main-navigation ul ul {
  background-color: #3f3f3f;
}
.main-navigation .main-nav ul ul li a {
  color: #ffffff;
}
.main-navigation .main-nav ul ul li:hover > a,
.main-navigation .main-nav ul ul li:focus > a,
.main-navigation .main-nav ul ul li.sfHover > a {
  color: #ffffff;
  background-color: #4f4f4f;
}
.main-navigation .main-nav ul ul li[class*="current-menu-"] > a {
  color: #ffffff;
  background-color: #4f4f4f;
}
.main-navigation .main-nav ul ul li[class*="current-menu-"] > a:hover,
.main-navigation .main-nav ul ul li[class*="current-menu-"].sfHover > a {
  color: #ffffff;
  background-color: #4f4f4f;
}
.separate-containers .inside-article,
.separate-containers .comments-area,
.separate-containers .page-header,
.one-container .container,
.separate-containers .paging-navigation,
.inside-page-header {
  background-color: #ffffff;
}
.entry-meta {
  color: #595959;
}
.entry-meta a,
.entry-meta a:visited {
  color: #595959;
}
.entry-meta a:hover {
  color: #1e73be;
}
.sidebar .widget {
  background-color: #ffffff;
}
.sidebar .widget .widget-title {
  color: #000000;
}
.footer-widgets {
  background-color: #ffffff;
}
.footer-widgets .widget-title {
  color: #000000;
}
.site-info {
  color: #ffffff;
  background-color: #222222;
}
.site-info a,
.site-info a:visited {
  color: #ffffff;
}
.site-info a:hover {
  color: #606060;
}
.footer-bar .widget_nav_menu .current-menu-item a {
  color: #606060;
}
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="number"],
textarea,
select {
  color: #666666;
  background-color: #fafafa;
  border-color: #cccccc;
}
input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="tel"]:focus,
input[type="number"]:focus,
textarea:focus,
select:focus {
  color: #666666;
  background-color: #ffffff;
  border-color: #bfbfbf;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"],
a.button,
a.button:visited,
a.wp-block-button__link:not(.has-background) {
  color: #ffffff;
  background-color: #666666;
}
button:hover,
html input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover,
a.button:hover,
button:focus,
html input[type="button"]:focus,
input[type="reset"]:focus,
input[type="submit"]:focus,
a.button:focus,
a.wp-block-button__link:not(.has-background):active,
a.wp-block-button__link:not(.has-background):focus,
a.wp-block-button__link:not(.has-background):hover {
  color: #ffffff;
  background-color: #3f3f3f;
}
.generate-back-to-top,
.generate-back-to-top:visited {
  background-color: rgba(0, 0, 0, 0.4);
  color: #ffffff;
}
.generate-back-to-top:hover,
.generate-back-to-top:focus {
  background-color: rgba(0, 0, 0, 0.6);
  color: #ffffff;
}
.entry-content .alignwide,
body:not(.no-sidebar) .entry-content .alignfull {
  margin-left: -40px;
  width: calc(100% + 80px);
  max-width: calc(100% + 80px);
}
.rtl .menu-item-has-children .dropdown-menu-toggle {
  padding-left: 20px;
}
.rtl .main-navigation .main-nav ul li.menu-item-has-children > a {
  padding-right: 20px;
}
@media (max-width: 768px) {
  .separate-containers .inside-article,
  .separate-containers .comments-area,
  .separate-containers .page-header,
  .separate-containers .paging-navigation,
  .one-container .site-content,
  .inside-page-header {
    padding: 30px;
  }
  .entry-content .alignwide,
  body:not(.no-sidebar) .entry-content .alignfull {
    margin-left: -30px;
    width: calc(100% + 60px);
    max-width: calc(100% + 60px);
  }
}
.one-container .sidebar .widget {
  padding: 0px;
}
@media (max-width: 768px) {
  .main-navigation .menu-toggle,
  .main-navigation .mobile-bar-items,
  .sidebar-nav-mobile:not(#sticky-placeholder) {
    display: block;
  }
  .main-navigation ul,
  .gen-sidebar-nav {
    display: none;
  }
  [class*="nav-float-"] .site-header .inside-header > * {
    float: none;
    clear: both;
  }
}
@font-face {
  font-family: "GeneratePress";
  src: url("../scss/fonts/generatepress.eot");
  src: url("../scss/fonts/generatepress.eot#iefix") format("embedded-opentype"),
    url("../scss/fonts/generatepress.woff2") format("woff2"),
    url("../scss/fonts/generatepress.woff") format("woff"),
    url("../scss/fonts/generatepress.ttf") format("truetype"),
    url("../scss/fonts/generatepress.svg#GeneratePress") format("svg");
  font-weight: normal;
  font-style: normal;
}
